/*--基本スタイル--*/
/*--mixin--*/
/*共通部分*/
html {
  font-size: 100%;
  letter-spacing: 0.22em;
  visibility: hidden;
  opacity: 0;
}

html.wf-active {
  visibility: visible;
  opacity: 1;
}

body {
  font-family: "Noto Sans JP", sans-serif, apple-system, blinkMacSystemFont, "Helvetica Neue", "Segoe UI", YuGothicM, YuGothic, Meiryo, "Yu Gothic Medium", "ヒラギノ角ゴ Pro W3", sans-serif;
  line-height: 1.5;
  color: #413D3E;
  font-weight: 400;
  letter-spacing: 1.3px;
}
@media screen and (max-width: 768px) {
  body {
    font-size: 0.875rem;
  }
}
@media screen and (max-width: 576px) {
  body {
    font-size: 0.75rem;
  }
}

body.fixed {
  width: 100%;
  position: fixed;
  overflow: hidden;
}

.max-extend {
  max-width: 1920px;
  margin: 0 auto;
}

.max-extend-inner {
  max-width: 1366px;
  margin: 0 auto 0;
}

.content-extend {
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
}

.wrapper {
  padding: 0 4%;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 2rem;
  line-height: 1;
}
@media screen and (max-width: 576px) {
  h1, h2, h3, h4, h5, h6 {
    font-size: 1.5rem;
  }
}

b {
  font-weight: 700;
}

p {
  line-height: 1.5;
  font-weight: 300;
  font-size: 1rem;
}
@media screen and (max-width: 768px) {
  p {
    font-size: 0.875rem;
  }
}
@media screen and (max-width: 576px) {
  p {
    font-size: 0.75rem;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  appearance: none;
}

.pc-br {
  display: block;
}
@media screen and (max-width: 768px) {
  .pc-br {
    display: none;
  }
}

.sp-br {
  display: none;
}
@media screen and (max-width: 768px) {
  .sp-br {
    display: block;
  }
}

.m-br {
  display: none;
}
@media screen and (max-width: 992px) {
  .m-br {
    display: block;
  }
}

.xs-br {
  display: none;
}
@media screen and (max-width: 576px) {
  .xs-br {
    display: block;
  }
}

.pc-show {
  display: block;
}
@media screen and (max-width: 768px) {
  .pc-show {
    display: none;
  }
}

.sp-show {
  display: none;
}
@media screen and (max-width: 768px) {
  .sp-show {
    display: block;
  }
}

.sp-xs-none {
  display: none;
}
@media screen and (max-width: 768px) {
  .sp-xs-none {
    display: block;
  }
}
@media screen and (max-width: 576px) {
  .sp-xs-none {
    display: none;
  }
}

.sp-xs-show {
  display: none;
}
@media screen and (max-width: 576px) {
  .sp-xs-show {
    display: block;
  }
}

ul {
  list-style: none;
}

select {
  appearance: revert;
}

table {
  border-collapse: collapse;
  width: 100%;
  text-align: left;
}
@media screen and (max-width: 576px) {
  table th, table td {
    font-size: 0.875rem;
  }
}

ol {
  padding-left: 20px;
}

ul {
  list-style: none;
}

.back-inherit {
  background-color: inherit !important;
}

.text-align-center {
  text-align: center;
}

.mr-top {
  margin-top: 110px;
}
@media screen and (max-width: 768px) {
  .mr-top {
    margin-top: 40px;
  }
}

.responsive-br {
  display: block;
}
@media screen and (max-width: 576px) {
  .responsive-br {
    display: none;
  }
}

.flex {
  display: flex;
}
@media screen and (max-width: 768px) {
  .flex {
    display: block;
  }
}

.flex-warp {
  flex-wrap: wrap;
}

.flex-as {
  display: flex;
}

.flex-reverse {
  flex-direction: row-reverse;
}

.justify-between {
  justify-content: space-between;
}
.justify-end {
  justify-content: flex-end;
}
.justify-around {
  justify-content: space-around;
}
.justify-center {
  justify-content: center;
}

.align-center {
  align-items: center;
}

.no-warp {
  white-space: nowrap;
}

.pointer {
  cursor: pointer;
}

/* フォントカラー */
.section-content {
  margin-top: 40px;
  margin-bottom: 80px;
}
@media screen and (max-width: 768px) {
  .section-content {
    margin-top: 32px;
    margin-bottom: 56px;
  }
}

/* bottom 三角 */
.triangle-bottom {
  margin: 48px auto;
  width: 0;
  height: 0;
  border-left: 250px solid transparent;
  border-right: 250px solid transparent;
  border-top: 64px solid #ffffff;
}
@media screen and (max-width: 576px) {
  .triangle-bottom {
    border-left: 150px solid transparent;
    border-right: 150px solid transparent;
    border-top: 50px solid #ffffff;
  }
}

.common-content img {
  width: 100%;
  object-fit: cover;
}

.inline-d {
  display: inline-block;
}

/* ヘッダー */
